@import "../../vars";

.semiBold {
  font-weight: $semi-bold;
}

.stat {
  display: flex;
  flex-direction: column;
  align-items: center;

  .keyStat {
    min-height: 112px;
    min-width: 112px;
    max-height: 112px;
    max-width: 112px;
    border: 2px solid rgba(0, 0, 0, 0.6);
    border-radius: 16px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }

  .subtitle {
    color: rgba(0, 0, 0, 0.67);
    margin-top: 4px;
    -webkit-hyphens: auto;
    -moz-hyphens: auto;
    -ms-hyphens: auto;
    hyphens: auto;
    text-align: center;
    font-weight: $medium;
  }
}

.grid {
  margin-bottom: 64px;
}

.alerts {
  display: flex;
  flex-direction: column;
  margin-bottom: 16px;
  max-height: 25vh;
  overflow: auto;
  .alert {
    border: 1px solid rgba(0, 0, 0, 0.6);
    border-radius: 4px;
    padding: 6px 8px;
    margin: 16px 0;
    cursor: pointer;

    &:hover {
      background-color: $color-neutral;
    }
  }
}